@import "./../../Variable.scss";

.message-container {
    width: 100%;
    height: 100vh;
    background: white;
    position: absolute;
    top: 0;
    z-index: 9999;
    // color: white;


    .message-container-wrapper {
        width: 100%;
        height: 100%;
        box-sizing: border-box;
        overflow-y: scroll;
        padding-top: 100px;

        .message-profile-div {
            width: 100%;
            display: flex;
            align-items: center;
            background: none;
            padding: 0.5rem;
            // border-bottom: 1px solid #ccc;
            position: absolute;
            top: 0;
            z-index: 9999;
            background: rgba(0, 0, 0, 0.021);
            backdrop-filter: blur(10px);
            box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;

            .message-arrow {
                margin: 0 0.5rem;
                margin-right: 1rem;
            }

            .message-profile-img {
                width: 40px;
                height: 40px;
                border-radius: 50%;
                object-fit: cover;
                object-position: center;
            }

            .message-profile-name {
                margin-left: 0.5rem;
            }

        }

        .message-list-container {

            width: 100%;
            height: 100%;
            display: inline-flex;
            flex-direction: column;
            align-items: start;
            justify-content: end;
            padding: 1rem;
            padding-top: 70px;
            padding-bottom: 60px;
        }

        .message-input-wrapper {
            display: flex;
            flex-direction: column;
            width: 100%;
            max-width: 556px;
            background: rgba(0, 0, 0, 0);
            backdrop-filter: blur(10px);
            position: absolute;
            bottom: 0;
            box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;

            .imgSelected-div {
                width: 100%;
                display: flex;
                justify-content: center;
                position: relative;

                .loading {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    z-index: 9999;
                }

                .imgSelected-img {
                    width: 100%;
                    max-width: 200px;
                    height: 150px;
                    object-fit: contain;
                    object-position: center;
                }

                .imgSelected-close-div {
                    width: 100%;
                    display: flex;
                    justify-content: end;
                    position: absolute;
                    padding-right: 1rem;
                    position: absolute;
                    top: 15px;

                    .imgSelected-close-img {
                        font-size: 24px;

                    }
                }
            }

            .message-input-wrapper-inner {
                display: flex;
                align-items: center;
                padding: 1rem;
                height: 50px;

                .message-input {
                    flex: 1;
                    font-size: 16px;
                    padding-inline-start: 10px;
                    border: none;
                    outline: none;
                    border-radius: 40px;
                    height: 35px;
                    margin: 0 1rem;
                }

                .message-camera {
                    font-size: 24px;
                }

                .message-send-btn {
                    font-size: 25px;
                }

                .message-thumb {
                    font-size: 24px;
                    margin-left: 1rem;
                }

            }

        }

    }


    .view-Reply {
        width: 100%;
        padding: 0 1rem;
        display: flex;
        align-items: center;
        background: rgba(0, 128, 0, 0.041);
        backdrop-filter: blur(10px);
        overflow: hidden;

        .view-replay-sms {
            flex: 1;
            // background: blue;
            overflow: hidden;
            display: flex;
            justify-content: center;
        }

        .view-close-btn {
            // background: red;
        }
    }

}

.alignment {
    display: flex;
    flex-direction: column;
    height: 100%;
    align-items: end;
    justify-content: end;
}

.sender {
    // background-color: lightblue;
    color: white;
    display: flex;
    justify-content: end !important;
    width: 100%;
}

.user {
    // background-color: lightgreen;
    color: black;
    justify-content: flex-start;
    width: 100%;
}

.message-item {
    display: flex;
    justify-content: flex-start;
    margin-bottom: 10px;
}


.message-bubble {
    display: inline-block;
    max-width: 80%;
    border-radius: 50px;
    padding: 6px 20px;
    color: white;

    &.message-sender {
        background: #2E9AFE;

        &.has-image {
            background: transparent;

            .last-conversation-time {
                color: #999;
            }
        }


    }

    &.message-recipient {

        &.has-image {
            border: none;
            background: transparent;
        }
    }
}

.photo-div {
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9999;
    background-color: rgba(0, 0, 0, 0.63);
    display: flex;
    justify-content: center;
    align-items: center;

    .photo-div-inner {
        width: 400px;
        height: 100%;
        padding-top: 60px;
        padding-bottom: 60px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;



        .photo-bg-div {
            width: 100%;
            height: 100%;
            background-size: contain;
            background-position: center;
            background-repeat: no-repeat;

            .photo-bg-inner-div {
                color: white;
                width: 100%;
                height: 100%;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: end;
                padding-right: 0.5rem;

                .photo-close-div {
                    width: 30px;
                    height: 30px;
                    background: linear-gradient(#2E9AFE, #0431B4);
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    border-radius: 50%;
                    margin: 1rem 0;
                    box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;

                    .photo-close {
                        font-size: 24px;
                    }

                    .photo-delete {
                        font-size: 24px;
                    }
                }

            }
        }

        .photo {
            width: 100%;
            max-width: 567px;
            height: 450px;
            object-fit: contain;
            object-position: center;
        }

    }
}

.message-sender {
    display: flex;
    align-items: center;
    justify-content: center;
    background: #2E9AFE;
    color: white;

    .last-conversation-time {
        font-size: 11px;
        color: whitesmoke;
    }
}

.message-recipient {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid rgba(0, 0, 0, 0.3);

    color: black;
    padding: 0.3rem;
    padding-right: 1rem;

    .last-conversation-time {
        font-size: 11px;
        color: #999;
    }

}



.message-content {
    margin-bottom: 5px;
}

.reply-button {
    margin-left: 1rem;
    font-size: 18px;
}

.bi-x-circle-fill {
    color: red;
    margin-right: 0.5rem;
}

.message-img {
    margin-right: 0.5rem;
    width: 35px;
    height: 35px;
    object-fit: cover;
    object-position: center;
    border-radius: 50%;
}

.messageImg {
    width: 100%;
    max-width: 100px;
    height: 150px;
    object-fit: cover;
    object-position: center;
    border-radius: 0.5rem;
}